.mainRoot {
    background-color: #000;
    width: 100%;
    height: 100%;
    overflow: scroll;
}
.list {
    border-color: #aa4444;
    border-style: solid;
    border-width: 5px;
}
.menu-btn {
    position: absolute;
    right: 10px;
    bottom: 5px;
    align-items: center;
    justify-content: center;
}

.center {
    display: flex;
    align-items: center;
    justify-content: center;
}

.step-container {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-around;
    padding: 4px 0px;
    width: 100%;
}

.step-text-container {
    flex-direction: column;;
    align-items: center;
    justify-content: space-around;
    padding: 0px 8px;
}

.step-text {
    font-size: 12px;
    color: #444;
}

.item-img-container {

}

.item-img {
    border-bottom-color: rgba(200, 200, 200, 0.3);
    border-bottom-width: 1px;
    width:100px;
    height: 100px;
}

.item-text-container {
    flex: 1;
    align-items: flex-start;
    justify-content: center;
    padding: 0px 15px;
}

.item-text {
    margin-top: 6px;
    font-size: 11px;
    line-height: 14px;
    color: #1d60b4;
    text-decoration-line: underline;
    padding-right: 15px;
}

.item-summary {
    padding: 4px 15px 0px 15px;
    text-align: left;
    color: #818181;
    font-size: 12px;
    line-height: 16px;
    background-color: transparent;
    margin-bottom: 8px;
}

.menu-page {
    display: flex !important;
    position: absolute;
    width: 50%;
    height: 100%;
    top: 0;
    bottom: 0;
    left: 50%;
    right: 0;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-color: rgba(0, 0, 0, 0.6);
}

.menu-page-list {
    flex: 1;
    width: 100%;
}