body {
    margin: 0;
    padding: 0;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
    text-align: center;
    background-color: darkgrey;
    width: 100%;
    height: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-flex: 1 1;
            flex: 1 1;
}
.mainRoot {
    background-color: #000;
    width: 100%;
    height: 100%;
    overflow: scroll;
}
.list {
    border-color: #aa4444;
    border-style: solid;
    border-width: 5px;
}
.menu-btn {
    position: absolute;
    right: 10px;
    bottom: 5px;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
}

.center {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
}

.step-container {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    padding: 4px 0px;
    width: 100%;
}

.step-text-container {
    -webkit-flex-direction: column;
            flex-direction: column;;
    -webkit-align-items: center;;
            align-items: center;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    padding: 0px 8px;
}

.step-text {
    font-size: 12px;
    color: #444;
}

.item-img-container {

}

.item-img {
    border-bottom-color: rgba(200, 200, 200, 0.3);
    border-bottom-width: 1px;
    width:100px;
    height: 100px;
}

.item-text-container {
    -webkit-flex: 1 1;
            flex: 1 1;
    -webkit-align-items: flex-start;
            align-items: flex-start;
    -webkit-justify-content: center;
            justify-content: center;
    padding: 0px 15px;
}

.item-text {
    margin-top: 6px;
    font-size: 11px;
    line-height: 14px;
    color: #1d60b4;
    -webkit-text-decoration-line: underline;
            text-decoration-line: underline;
    padding-right: 15px;
}

.item-summary {
    padding: 4px 15px 0px 15px;
    text-align: left;
    color: #818181;
    font-size: 12px;
    line-height: 16px;
    background-color: transparent;
    margin-bottom: 8px;
}

.menu-page {
    display: -webkit-flex !important;
    display: flex !important;
    position: absolute;
    width: 50%;
    height: 100%;
    top: 0;
    bottom: 0;
    left: 50%;
    right: 0;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    background-color: rgba(0, 0, 0, 0.6);
}

.menu-page-list {
    -webkit-flex: 1 1;
            flex: 1 1;
    width: 100%;
}
